import styled, { css }   from 'styled-components'
import TwoRow            from "../../components/Layout/twoRow";
import Slideshow         from '../../components/Slideshow'



const AbstractDiv = styled.div`
    width: 90%;
    height: auto;
`

const Top = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
`

const Bottom = styled.div`
    display: flex;
    flex-direction: row;
    margin: 2% 0;
    align-items: flex-start;
    justify-content: center;   
`

const DetailTitle = styled.p`
    color: #AE69FF;
    font-size: 2.5vw;
    font-weight: bolder;
    width: auto;
    height: auto;
 

    &::selection {
        background: #AE69FF;
        color: #FFFFFF;
    }

    &::-moz-selection {
        background: #AE69FF;
        color: #FFFFFF;
    }
`

const Name = styled.span`
    width: auto;
    height: auto;
    font-weight: bolder;
    font-size: 2.5vw;
    text-align:center;

    &::selection {
        background: #AE69FF;
        color: #FFFFFF;
    }

    &::-moz-selection {
        background: #AE69FF;
        color: #FFFFFF;
    }
`

const Group = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
`


const DetailP = styled.p`
    color: #AE69FF;
    margin-top: 2rem;
    font-size  : 1.2vw;

    & div {
        &::selection {
            background: #AE69FF;
            color: #FFFFFF;
        }

        &::-moz-selection {
            background: #AE69FF;
            color: #FFFFFF;
        }

    }

    ${props => !props.isMobile && css`
        display: flex;
        align-items: center;
    `}
`

const Date = styled.p`
    color: #AE69FF;
    font-size: 2.5vw;
    text-decoration: underline;
    width: auto;   
    font-weight: bolder;
    padding: 1%; 
`

function Abstract({ image, name, date, tag }) {
    console.log(name)
    return (
        <AbstractDiv>
            <Top>
                <Slideshow images={image} />
            </Top>
            <Bottom>
                <Group>
                    <Date>{date}</Date>
                    <Name>
                        {name[0]}
                        {name.length > 0 && <><br/>{name[1]}</>}
                    </Name>
                </Group>
            </Bottom>

        </AbstractDiv>
    )
}

function Detail({ title, content }) {
    return (
        <>
            <DetailTitle>{title}</DetailTitle>
            <DetailP><div dangerouslySetInnerHTML={{ __html: content }} /></DetailP>
        </>
    )

}





function DesktopArtistDetail({ image, name, title, tag, date, content }) {
    return (
        <TwoRow 
            top={true}
            left={<Abstract image={image} name={name} date={date} tag={tag} />} 
            right={<Detail title={title} content={content} />}
        />
    )
}

export default DesktopArtistDetail;